<template>
    <div class="apply">
        <nav-bar title="我要申诉"/>
        <div class="container">
            <div class="item">
                <label>申诉原因<span>(必填)</span></label>
                <fui-textarea
                    v-model="reanson"
                    :required="true"
                    :length="5"
                    valiedate-message="原因不能未空"
                    placeholder="请描述申请原因"
                    :hanle-validate="hanleValidate"
                />
            </div>
            <div class="item">
                <div
                    class="tip"
                    @click="openTeachDialog">
                    查看样例
                </div>
                <label>自拍认证<span>(必填)</span></label>
                <div
                    class="photo"
                    v-if="photo">
                    <img
                        class="photo__user"
                        :src="photo.imageUrl"
                        @click="openPhoto"
                    >
                    <div
                        class="close"
                        @click="cancalPhoto"
                    />
                </div>

                <div
                    v-else
                    class="photo__empty"
                    @click="takePhoto"
                >点击拍摄</div>
            </div>
            <div class="item">
                <label>手机号码<span>(必填)</span></label>
                <fui-input
                    v-model="tel"
                    type="phone"
                />
            </div>
        </div>
        <div class="footer">
            <button
                @click="submit"
                :class="{
                    actived: formReady,
                }"
            >提交申诉</button>
        </div>
        <gray-mask :show="dialog">
            <Dialog
                :show.sync="dialog"
                btnText="我知道了"
                :clickHandler="closeDialog"
            >
                <h3
                    class="dialog__title"
                    slot="title"
                >
                    已提交
                </h3>
                <div
                    slot="msg"
                    class="dialog__msg"
                >
                    <p>48小时内反馈申诉结果</p>
                    <p>请注意查看</p>
                </div>
            </Dialog>
        </gray-mask>
        <Dialog
            class="teach"
            :show.sync="showTeach"
        >
            <div class=teach__box>
                <div class="teach__top" />
                <div class="teach__content">
                    <div class="teach__person" />
                    <div class="teach__mask" />
                </div>
                <div class="teach__bottom">
                    <div class="teach__finger" />
                </div>
            </div>
        </Dialog>
    </div>
</template>

<script>
import NavBar from '@/components/NavBar';
import fuiInput from '@f2f/feed-ui/packages/input/index.vue';
import GrayMask from '@/components/GrayMask';
import { addCustomEventListener } from '@/utils/event';
import Dialog from '@/components/Dialog';
import { feedbackApply } from '@/api/withdraw';
import {
    toast,
    takePhoto,
    quitCurrentWebview,
    isTakeOverBackByWeb } from '@/utils/jsApiV2/client';

const fuiTextarea = fuiInput.fuiTextarea;

export default {
    components: {
        NavBar,
        fuiInput,
        fuiTextarea,
        GrayMask,
        Dialog,
    },
    data() {
        return {
            reanson: '',
            tel: '',
            photo: false,
            dialog: false,
            showTeach: false,
        };
    },
    computed: {
        formReady() {
            if (this.reanson.length > 4 && this.reanson.length < 201 &&
                    this.tel.length === 11 && this.photo) {
                return true;
            }
            return false;
        },
    },
    created() {
        this.$root.hidePlaceholder();
        addCustomEventListener('backKey', this.backKeyHandler);
    },
    watch: {
        dialog(show) {
            if (!show) {
                quitCurrentWebview();
            }
        },
        showTeach(v) {
            isTakeOverBackByWeb(v);
        },
    },
    methods: {
        takePhoto() {
            window.withdrawTakePhotoCallBack = this.updatePhoto;
            takePhoto('withdrawTakePhotoCallBack', () => {
                toast('升级最新版APP才能使用该功能');
            });
        },
        openPhoto() {
            toast('暂未支持预览');
        },
        cancalPhoto() {
            this.photo = false;
        },
        updatePhoto(photo) {
            if (photo) {
                this.photo = photo;
            } else {
                toast('选取照片失败');
            }
        },
        closeDialog() {
            this.dialog = false;
        },
        submit() {
            if (!this.formReady) {
                return;
            }
            feedbackApply({
                category: '申诉',
                type: '问题',
                phone: this.tel,
                imageInfo: JSON.stringify([this.photo]),
                content: this.reanson.slice(0, 200),
            }).then(() => {
                this.dialog = true;
            }).catch(() => {
                toast('操作失败了，再试试');
            });
        },
        openTeachDialog() {
            this.showTeach = true;
        },
        backKeyHandler() {
            this.showTeach = false;
        },
        hanleValidate(value) {
            if (value.length > 200) {
                return {
                    validate: false,
                    valiedateMessage: '字数不能大于200字',
                };
            }
            return {
                validate: true,
                valiedateMessage: '',
            };
        },
    },
};
</script>

